/*我的工单main组件*/
<template>
  <div class="mainCon">
    <div class="mainTop">
      <div class="mainTopLeft">
        <el-button type="primary" size="mini" @click="addWorkOrder"><i class="el-icon-plus "></i>添加</el-button>
        <el-button type="warning" size="mini" @click="changeWorkOrder"><i class="el-icon-edit "></i>修改</el-button>
        <el-button type="danger" size="mini" @click="deleteWorkOrder"><i class="el-icon-close "></i>删除</el-button>
        <el-button type="primary" size="mini" @click="uploadFile"><i class="iconfont icon_upload"></i>上传附件</el-button>
        <el-button type="success" icon="el-icon-refresh"  size="mini">刷新</el-button>
      </div>
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[50, 100, 200]"
          :page-size="50"
          :pager-count="pagerCount"
          layout="sizes, prev, pager, next"
          :total="total"
        ></el-pagination>
      <div class="common">共{{ count }}笔</div>
    </div>
    <div class="mainBottom">
      <el-table
        :data="tableData"
        style="width: 100%"
        height="79vh"
        stripe
        border
        v-loading='tableLoading'
        @selection-change="handleSelectionChange">
        <el-table-column
          fixed
          type="index"
          min-width="50">
        </el-table-column>
        <el-table-column
          fixed
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          fixed
          show-overflow-tooltip
          prop="workorder_id"
          label="单号"
          min-width="50">
        </el-table-column>
        <el-table-column
          fixed
          show-overflow-tooltip
          prop="types_alias"
          label="类别"
          min-width="50">
        </el-table-column>
        <el-table-column
          fixed
          show-overflow-tooltip
          prop="title"
          label="标题"
          min-width="100">
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          label="附件"
          min-width="50">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="downloadFile(scope.row.attachment)">下载</el-button>
          </template>
        </el-table-column>
        <el-table-column
          prop="describe"
          show-overflow-tooltip
          label="描述">
        </el-table-column>
        <el-table-column
          prop="processing_opinion"
          show-overflow-tooltip
          label="处理意见"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="status_alias"
          show-overflow-tooltip
          label="状态"
          min-width="100">
        </el-table-column>
        <el-table-column
          prop="process_at"
          show-overflow-tooltip
          label="处理时间"
          min-width="100">
        </el-table-column>
        <el-table-column
          show-overflow-tooltip
          prop="created_at"
          label="创建时间"
          min-width="100">
        </el-table-column>
      </el-table>
    </div>
    <el-dialog title="编辑工单" :visible.sync="dialogFormVisible" class="editWorkOrder1" width="40%">
      <el-form :model="ruleForm" size="mini" :rules="rules" ref="ruleForm">
        <el-form-item label="标题" :label-width="formLabelWidth" prop="title">
          <el-input v-model="ruleForm.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="类别" :label-width="formLabelWidth" prop="types">
          <el-select v-model="ruleForm.types" placeholder="请选择类别" @change="handleChange">
            <el-option v-for="(item,index) in types" :key="index" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="describe">
          <el-input
            type="textarea"
            :rows="4"
            placeholder="请输入备注信息"
            v-model="ruleForm.describe">
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogFormVisible = false">取 消</el-button>
        <el-button size="small" type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
    
    <!-- 修改弹出框 -->
    <el-dialog title="编辑工单" :visible.sync="dialogFormVisibleTwo">
      <el-form :model="ruleFormTwo" size="mini" :rules="rules" ref="ruleFormTwo">
        <el-form-item label="标题" :label-width="formLabelWidth" prop="title">
          <el-input v-model="ruleFormTwo.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="类别" :label-width="formLabelWidth" prop="types">
          <el-select v-model="ruleFormTwo.types" placeholder="请选择类别" @change="handleChange">
            <el-option v-for="(item,index) in types" :key="index" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="describe">
          <el-input
            type="textarea"
            :rows="4"
            placeholder="请输入备注信息"
            v-model="ruleFormTwo.describe">
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogFormVisibleTwo = false">取 消</el-button>
        <el-button size="small" type="primary" @click="submitFormTwo('ruleFormTwo')">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 添加图片信息开始 -->
      <el-dialog title="添加附件" :visible.sync="dialogImageVisible" center @close="cancelAdd">
        <uploadFileCom :uploadFileData="uploadFileConOne" />
        <div slot="footer" class="dialog-footer">
          <el-button size="small" @click="cancelAdd">取 消</el-button>
          <el-button size="small" type="primary" @click="addImgList">确 定</el-button>
        </div>
      </el-dialog>
      <!-- 添加图片信息结束 -->

  </div>
</template>

<script>
  import uploadFileCom from '@/components/uploadFile'
export default {
  data(){
    let validateName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入标题'));
      } else {
        callback();
      }
    };
    let validateRegion = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请选择类型'));
      } else {
        callback();
      }
    };
    let validateRemarks = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入备注信息'));
      } else {
        callback();
      }
    };
    return {
      total: 0,//该分类产品共有多少条数据（非共多少笔）
      count: 120,
      currentPage: 1,//第几页
      pagerCount: 5,//
      dialogImageVisible: false,//添加附件弹出框
      uploadFileConOne: {
        limit: 1,
        uploadSrc: '/v1/attachmentUpload',
        multiple: false,
        buttonState: false,
        imgList: [],
        comName: 'fileCom'
      },
      uploadFileData: '',//附件信息
      // tableColumnState: false,//表格展开行显示状态
      dialogFormVisible: false,//弹出框显示状态
      dialogFormVisibleTwo: false,//弹出框显示状态
      tableData: [],
      multipleSelection: [],
      activeName: 'first',//标签页默认显示
      ruleForm: {//数据临时操作容器
        title: '',
        types: '',
        describe: ''
      },
      ruleFormTwo: {//数据临时操作容器
        title: '',
        types: '',
        describe: '',
        id: ''
      },
      addRuleForm: {},//添加工单数据容器
      changeRuleForm: {},//修改工单数据容器
      formLabelWidth: "100px",
      //添加工单（下拉菜单类别）
      types: [
        { value: '1', label: '催单' },
        { value: '2', label: '物流' },
        { value: '3', label: '程序' },
        { value: '4', label: '取消订单' },
        { value: '5', label: '重寄' },
        { value: '6', label: '退款' },
        { value: '7', label: '其他' },
      ],
      rules: {
        title: [ { validator: validateName, trigger: 'blur' } ],
        types: [ { validator: validateRegion, trigger: 'change' } ],
        describe: [ { validator: validateRemarks, trigger: 'blur' } ]
      },
      tableLoading: false,
    }
  },
  components: {
    uploadFileCom
  },
   methods: {
     uploadFile(){
       if(this.multipleSelection.length!=1){
         this.$message({
           type: 'warning',
           message: '选择一条记录进行上传'
         })
       }else {
         this.dialogImageVisible = true;
       }
     },
     //取消上传
     cancelAdd(){
       this.dialogImageVisible = false;
       this.$store.commit('CHNAGE_FILE_DATA','');
     },
     //上传附件
     addImgList(){
       let formData = new FormData();
       formData.append('attachment',this.uploadFileData[0].raw);
       this.imgRequest(`/v1/attachmentUpload?id=${this.multipleSelection[0].id}`,formData)
       .then(res => {
         if(res.data.code=='200'){
           this.$message({
             type: 'success',
             message: '上传成功'
           })
           this.cancelAdd();
           this.initData();
         }
       })
     },
     downloadFile(fileUrl){
       window.location.href = fileUrl;
     },
     //添加工单
    addWorkOrder(){
      this.dialogFormVisible = true;
    },
    //修改工单
    changeWorkOrder(){
      if(this.multipleSelection.length != 1){
        this.$message({
          message: '请选择一条记录进行修改',
          type: 'warning'
        });
      }else {
        this.dialogFormVisibleTwo = true;
        this.ruleFormTwo.id = this.multipleSelection[0].id;
        this.ruleFormTwo.title = this.multipleSelection[0].title;
        this.ruleFormTwo.types = this.multipleSelection[0].types;
        this.ruleFormTwo.describe = this.multipleSelection[0].describe;
      }
    },
    //删除工单
    deleteWorkOrder(){
      if(this.multipleSelection.length<=0){
        this.$message({
          message: '请选择需要删除的工单',
          type: 'warning'
        });
      }else {
        let orderId = [];
        for (let index = 0; index < this.multipleSelection.length; index++) {
          const element = this.multipleSelection[index];
          orderId.push(element.id);
        }
        orderId = orderId.join(',');
        this.postRequest(`/v1/workOrderDelete?id=${orderId}`)
        .then(res => {
          if(res.data.data=='1'){
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.initData();
          }else {
             this.$message.error('删除失败');
          }
        }).catch(error => {
        })
        // ;
      }
    },
    //添加工单选择类别
    handleChange(val){
    },
      //改变每页多少条
    handleSizeChange(val) {
      this.$store.commit('CHANGE_MYWORKER_LIMIT',val);
    },
   //改变页码
    handleCurrentChange(val) {
      this.$store.commit('CHANGE_MYWORKER_PAGE',val);
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    submitForm(formName,u_id=sessionStorage.getItem('selectUId')) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postRequest(`/v1/workorderAdd?u_id=${u_id}`,this.ruleForm)
          .then(res => {
            if(res.data.data=='1'){
              this.dialogFormVisible = false;
              this.$message({
                message: '添加成功',
                type: 'success'
              });
              this.initData();
            }else {
              this.dialogFormVisible = false;
              this.$message.error('添加失败');
            }
          }).catch(error => {
          })
        } else {
          return false;
        }
      });
    },
    //修改工单
    submitFormTwo(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postRequest('/v1/workOrderEdit',this.ruleFormTwo)
          .then(res => {
            if(res.data.data=='1'){
              this.dialogFormVisibleTwo = false;
              this.$message({
                message: '修改成功',
                type: 'success'
              });
              this.initData();
            }else {
              this.dialogFormVisibleTwo = false;
              this.$message.error('修改失败');
            }
          }).catch(error => {
          })
        } else {
          return false;
        }
      });
    },
    //初始化数据
    initData(){
      this.tableLoading = true
      this.postRequest(`/v1/workorderList`,this.$store.state.myWorkOrderData)
      .then(res => {
        this.tableData = res.data.data.data;
        this.total = res.data.data.total;
        this.count = res.data.data.count;
        this.tableLoading = false
      }).catch(error => {
      })
    }
  },
  created(){
    this.initData();
  },
  computed: {
    limit(){
      return this.$store.state.myWorkOrderData.limit;
    },
    title(){
      return this.$store.state.myWorkOrderData.title;
    },
    status(){
       return this.$store.state.myWorkOrderData.status;
    },
    typesState(){
      return this.$store.state.myWorkOrderData.types;
    },
    page(){
      return this.$store.state.myWorkOrderData.page;
    },
    fileData(){
      return this.$store.state.fileData;
    }
  },
  watch: {
    limit: function(newVal,oldVal){
      this.initData();
    },
    title: function(newVal,oldVal){
      this.initData();
    },
    status: function(newVal,oldVal){
      this.initData();
    },
    typesState: function(newVal,oldVal){
      this.initData();
    },
    page: function(newVal,oldVal){
      this.initData();
    },
    fileData: function(newVal,oldVal){
      this.uploadFileData = newVal;
    }
  }
}
</script>


<style lang="scss" scoped>
.mainCon {
  width: 100%;
  font-size: 12px;
  text-align: left;
  .mainTop {
    width: 100%;
    padding: 10px 20px;
    margin:10px 0;
    border:1px solid #dcdfe6;
    background-color: #fff;
    .mainTopLeft,.common {
      display: inline-block;
      text-align: right;
      line-height: 28px;
    }
    .mainTopLeft {
      text-align: left;
      i {
        margin-right: 5px;
      }
      .orderList {
        margin: 0 5px;
      }
    }
    .common {
      float: right;
      width: 50px;
    }
  }
  .mainBottom {
    height: 70vh;
    overflow: auto;
    img {
      width: 50px;
      cursor: pointer;
    }
  }
  .colorCon {
    width: 100%;
    display: inline-block;
    vertical-align: top;
  }
  .specCon {
    display: inline-block;
    width: 45%;
    vertical-align: top;
  }
}
