      this.$store.commit('CHANGE_MYWORKER_STATUS',val);
    },
    //更新类别
    handleChangeTwo(val){
      this.$store.commit('CHANGE_MYWORKER_TYPES',val);
    },
    handleClick(){
      if(this.inputValue != this.searchValue){
        this.$store.commit('CHANGE_MYWORKER_TITLE',this.searchValue);
        this.searchValue = '';
      }else {
        return false;
      }
    }
  },
}
